@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

/*adaptive for mobile and other width*/
@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
@custom-media --x2-resolution (resolution > 1.5dppx);
:root {
  --color__green: #68b738;
  --color__deep-green: #5eaa2f;
  --color__white: #ffffff;
  --color__black: #000000;
  --color__light-black: #111111;
  --color__grey: #f2f2f2;
  --color__deep-grey: #eaeaea;
  --color__dark-grey: #444444;
  --color__light-grey: #cdcdcd;
  --color__form-input-error: #ff8282;
}

.page__body {
  display: flex;
  min-width: 320px;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: var(--font_mobi-size);
}
@media (--not-narrow-vp) {
  .page__body {
    font-size: var(--font_default-size);
  }
}

.page__main {
  flex: 1;
}

.page__centerer {
  margin: 0 20px;
}
@media (--middle-vp) {
  .page__centerer {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    margin: 0 auto;
    width: 1220px;
  }
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: relative;
  display: flex;
  padding-top: 10px;
  padding-bottom: 2px;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color__white);
}
@media (--not-narrow-vp) {
  .header {
    padding-top: 35px;
  }
}
@media (--narrow-vp) {
  .header--noJS {
    display: block;
  }
  .header--noJS .menu-toggle {
    display: none;
  }
  .header--noJS .header-nav {
    display: block;
    position: static;
    width: 100%;
    opacity: 1;
  }
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: 0.3s;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

@media (--middle-vp) {
  .logo__icon {
    width: 60px;
    height: 52px;
  }
}
@media (--wide-vp) {
  .logo__icon {
    width: 70px;
    height: auto;
  }
}

.logo__text {
  transform: translateY(3px);
}
@media (--not-narrow-vp) {
  .logo__text [id=energy] {
    fill: var(--color__dark-grey);
  }
}
@media (--wide-vp) {
  .logo__text {
    height: 20px;
    width: auto;
  }
}

.menu-toggle {
  position: relative;
  top: 3px;
  display: block;
  width: 24px;
  height: 18px;
  background: transparent;
  border-top: 1.5px solid var(--color__black);
  border-bottom: 1.5px solid var(--color__black);
  border-left: none;
  border-right: none;
  font-size: 0;
  cursor: pointer;
  outline: none;
  transition: 0.2s;
  transition-timing-function: ease-in-out;
}
@media (--not-narrow-vp) {
  .menu-toggle {
    display: none;
  }
}
@media (--narrow-vp) {
  .menu-toggle--noJS {
    display: none;
  }
}
.menu-toggle::before, .menu-toggle::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: var(--color__black);
  transform: translate(-50%, -50%);
  transition: 0.2s;
  transition-timing-function: ease-in-out;
}
.menu-toggle--active {
  border-color: transparent;
}
.menu-toggle--active::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.menu-toggle--active::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

@media (--narrow-vp) {
  .header-nav {
    display: none;
    position: absolute;
    top: -145px;
    left: -20px;
    width: 100vw;
    opacity: 0;
    transition: 0.3s;
    text-align: center;
  }
  .header-nav--active {
    display: block;
    transform: translateY(201px);
    opacity: 1;
    z-index: 3;
  }
}
@media (--not-narrow-vp) {
  .header-nav {
    width: 62%;
  }
}
@media (--wide-vp) {
  .header-nav {
    width: 37%;
    z-index: 2;
  }
}
@media (--wide-vp) {
  .header-nav--color_change {
    --changed-color: var(--color__white);
  }
}

.header-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--narrow-vp) {
  .header-nav__list {
    display: flex;
    background-color: var(--color__white);
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}
@media (--not-narrow-vp) {
  .header-nav__list {
    position: static;
    display: flex;
    justify-content: flex-end;
    column-gap: 7.7%;
    opacity: 1;
  }
}
@media (--wide-vp) {
  .header-nav__list {
    position: static;
    display: flex;
    background-color: transparent;
    opacity: 1;
  }
}

@media (--narrow-vp) {
  .header-nav__item {
    position: relative;
    width: 100vw;
    display: flex;
    justify-content: center;
  }
  .header-nav__item::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #eaeaea;
    content: "";
  }
  .header-nav__item::after {
    position: absolute;
    top: 68px;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #eaeaea;
    content: "";
  }
}

.header-nav__link {
  position: relative;
  display: block;
  width: max-content;
  color: var(--changed-color, --color__black);
  text-transform: uppercase;
  text-decoration: none;
  font-size: 1.125rem;
  line-height: 1.3333333333;
  transition: 0.3s;
}
@media (--narrow-vp) {
  .header-nav__link {
    box-sizing: border-box;
    padding: 22px 24px;
    font-weight: 500;
  }
}
@media (--wide-vp) {
  .header-nav__link {
    font-size: 1.25rem;
    line-height: 1.482;
  }
}
@media (--not-narrow-vp) {
  .header-nav__link--current::before {
    position: absolute;
    top: 40px;
    right: 0;
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--color__green);
  }
}
@media (--wide-vp) {
  .header-nav__link--changed_underline::before {
    background-color: var(--changed-color);
  }
}
.header-nav__link:hover {
  opacity: 0.6;
}
.header-nav__link:active {
  opacity: 0.3;
}

.promo-container {
  padding-bottom: 20px;
  background-image: linear-gradient(to top, #ffffff 198px, transparent 198px), linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/happy-cat-s@1x.jpg");
  background-repeat: no-repeat;
  background-color: #29530f;
  background-position: 50% 0;
}
@media (--x2-resolution) {
  .promo-container {
    background-image: linear-gradient(to top, #ffffff 198px, transparent 198px), linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/happy-cat-s@2x.jpg");
    background-size: auto, auto, 320px auto;
  }
}
@media (--middle-vp) {
  .promo-container {
    background-image: none;
    background-color: var(--color__white);
  }
}
@media (--wide-vp) {
  .promo-container {
    position: relative;
    background-image: none;
    background-color: var(--color__white);
  }
}
@media (--wide-vp) {
  .promo-container::after {
    content: "";
    position: absolute;
    top: -18%;
    right: 0;
    width: 50%;
    height: 695px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/happy-cat-l@1x.jpg");
    background-color: #2e5c12;
    background-position: right top;
    background-repeat: no-repeat;
  }
}
@media (--x2-resolution) {
  .promo-container::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/happy-cat-l@2x.jpg");
    background-size: auto, 720px auto;
  }
}

