    font-size: 2.25rem;
  }
}
@media (--not-narrow-vp) {
  .promo__title {
    font-size: 3.75rem;
  }
}
@media (--narrow-vp) {
  .promo__title {
    line-height: calc(36 / 36);
  }
}
@media (--not-narrow-vp) {
  .promo__title {
    line-height: calc(60 / 60);
  }
}
@media (--not-narrow-vp) {
  .promo__title {
    color: var(--color__black);
    text-align: left;
    margin: 19px 0 42px 0;
    width: 430px;
  }
}

.promo__text {
  color: var(--color__white);
  margin-top: 0;
  margin-bottom: 29px;
  text-transform: uppercase;
  text-align: center;
  width: 75%;
}
@media (--narrow-vp) {
  .promo__text {
    font-size: 0.875rem;
  }
}
@media (--not-narrow-vp) {
  .promo__text {
    font-size: 1.25rem;
  }
}
@media (--narrow-vp) {
  .promo__text {
    line-height: calc(14 / 14);
  }
}
@media (--not-narrow-vp) {
  .promo__text {
    line-height: calc(20 / 20);
  }
}
@media (--not-narrow-vp) {
  .promo__text {
    color: var(--color__black);
    margin-bottom: 40px;
    text-align: left;
  }
}

.promo__image {
  display: block;
