}
@media (--middle-vp) {
  .promo__image {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 709px;
    height: auto;
  }
}
@media (--wide-vp) {
  .promo__image {
    position: absolute;
    top: 25px;
    left: 37%;
    width: 552px;
    height: auto;
    z-index: 3;
  }
}

.promo__link {
  box-sizing: border-box;
  width: 100%;
  z-index: 3;
}
@media (--not-narrow-vp) {
  .promo__link {
    box-sizing: border-box;
    padding-top: 10px;
    padding-bottom: 7px;
    width: 38%;
  }
}
@media (--wide-vp) {
  .promo__link {
    box-sizing: border-box;
    padding-top: 10px;
    padding-bottom: 7px;
    width: 20.5%;
  }
}

.main-link {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: var(--color__green);
  text-transform: uppercase;
  text-align: center;
  border: none;
  box-sizing: border-box;
  padding: 11px 20px;
  font-family: var(--accent-font);
  font-weight: 400;
  transition: 0.3s;
  cursor: pointer;
  text-decoration: none;
  color: white;
}
@media (--narrow-vp) {
  .main-link {
    font-size: 1rem;
  }
}
@media (--not-narrow-vp) {
  .main-link {
    font-size: 1.25rem;
  }
}
@media (--narrow-vp) {
  .main-link {
    line-height: calc(20 / 16);
  }
}
@media (--not-narrow-vp) {
  .main-link {
    line-height: calc(20 / 20);
  }
}
.main-link:hover {
  background-color: #5eaa2f;
}
.main-link:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (--narrow-vp) {
  .main-link--smaller-text {
    font-size: 0.875rem;
  }
}
@media (--not-narrow-vp) {
  .main-link--smaller-text {
    font-size: 1.25rem;
  }
}
@media (--narrow-vp) {
  .main-link--smaller-text {
    line-height: calc(18 / 14);
  }
}
@media (--not-narrow-vp) {
  .main-link--smaller-text {
    line-height: calc(20 / 20);
  }
}
@media (--not-narrow-vp) {
  .main-link--smaller-text {
    padding: 17px 5px;
