}

.health-programs {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  flex-wrap: wrap;
  padding-bottom: 13px;
}
@media (--wide-vp) {
  .health-programs {
    flex-direction: row;
    justify-content: space-between;
  }
}

.program {
  position: relative;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background-color: var(--color__grey);
  min-height: 266px;
}
@media (--middle-vp) {
  .program {
    padding-right: 350px;
    min-height: 372px;
  }
}
@media (--wide-vp) {
  .program {
    width: 570px;
    min-height: 374px;
  }
}
.program--slim {
  padding: 30px 33px 12px 19px;
}
@media (--not-narrow-vp) {
  .program--slim {
    padding: 74px 33px 12px 19px;
  }
}
@media (--wide-vp) {
  .program--slim {
    padding: 77px 33px 12px 19px;
  }
}
.program--fat {
  padding: 25px 33px 12px 19px;
}
@media (--not-narrow-vp) {
  .program--fat {
    padding: 78px 30px 12px 19px;
  }
}

.program__title {
  position: relative;
  display: flex;
  padding-left: 72px;
  padding-right: 5%;
  box-sizing: border-box;
  margin: 0;
  align-items: center;
  text-transform: uppercase;
  font-weight: 400;
}
@media (--narrow-vp) {
  .program__title {
    font-size: 1.5rem;
  }
}
@media (--not-narrow-vp) {
  .program__title {
    font-size: 2.25rem;
  }
}
@media (--narrow-vp) {
  .program__title {
    line-height: calc(37 / 24);
  }
}
@media (--not-narrow-vp) {
  .program__title {
    line-height: calc(36 / 36);
  }
}
@media (--middle-vp) {
  .program__title {
    padding-left: 34px;
    padding-right: 35%;
  }
}
@media (--wide-vp) {
  .program__title {
    padding-left: 195px;
  }
}
.program__title--slim::after {
  top: 0;
  left: 0;
  width: 70px;
  height: 70px;
  background-image: url("../icons/sprite.svg#cat-slim");
  background-size: auto;
}
@media (--middle-vp) {
  .program__title--slim::after {
    top: 5px;
    left: 60%;
    width: 270px;
    height: 270px;
  }
}
@media (--wide-vp) {
  .program__title--slim::after {
    top: -40px;
    left: 5%;
    width: 120px;
    height: 136px;
  }
}
.program__title--fat::after {
  top: 5px;
  left: -6px;
  width: 70px;
  height: 70px;
  background-image: url("../icons/sprite.svg#cat-pro");
  background-size: auto;
}
@media (--middle-vp) {
  .program__title--fat::after {
    top: 5px;
    left: 60%;
    width: 270px;
    height: 270px;
  }
}
@media (--wide-vp) {
  .program__title--fat::after {
    top: -40px;
    left: 2%;
    width: 134px;
    height: 130px;
  }
}
.program__title::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}
.program__title::before {
  content: "";
  position: absolute;
  top: -6px;
  left: 3px;
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--color__green);
}
@media (--middle-vp) {
  .program__title::before {
    width: 200px;
    height: 200px;
    top: 15px;
    left: 65%;
  }
}
@media (--wide-vp) {
  .program__title::before {
    width: 100px;
    height: 100px;
    top: -29px;
    left: 33px;
  }
}

.program__description {
  padding: 29px 3px 32px 0;
  margin: 0;
  color: var(--color__dark-grey);
  font-family: var(--main-font);
}
@media (--narrow-vp) {
  .program__description {
    font-size: 0.875rem;
  }
}
@media (--not-narrow-vp) {
  .program__description {
    font-size: 1rem;
  }
}
@media (--narrow-vp) {
  .program__description {
    line-height: calc(18 / 14);
  }
}
@media (--not-narrow-vp) {
  .program__description {
    line-height: calc(24 / 16);
  }
}
@media (--not-narrow-vp) {
  .program__description {
    padding: 27px 43% 40px 33px;
  }
}
@media (--wide-vp) {
  .program__description {
    padding: 75px 20px 15px 33px;
  }
}
.program__description::after {
  position: absolute;
  top: 78%;
  left: 0;
  display: block;
  width: 85%;
  transform: translateX(20px);
  height: 1px;
  background-color: #d9d9d9;
  content: "";
}
@media (--not-narrow-vp) {
  .program__description::after {
    display: none;
  }
}

.program__link {
  display: grid;
  padding: 10px 5px 10px 0;
  grid-template-columns: auto 1fr;
  align-items: center;
  margin-top: auto;
  box-sizing: border-box;
  text-transform: uppercase;
  transition: 0.3s;
  text-decoration: none;
  color: var(--color__black);
}
@media (--narrow-vp) {
  .program__link {
    font-size: 1rem;
  }
}
@media (--not-narrow-vp) {
  .program__link {
    font-size: 1.25rem;
  }
}
@media (--narrow-vp) {
  .program__link {
    line-height: calc(16 / 16);
  }
}
@media (--not-narrow-vp) {
  .program__link {
    line-height: calc(29.6 / 20);
  }
}
@media (--not-narrow-vp) {
  .program__link {
    padding: 10px 0 12px 33px;
  }
}
.program__link:hover::after {
  mask-position: center;
}
.program__link::after {
  position: relative;
  display: block;
  margin-left: 25px;
  width: 32px;
  height: 10px;
  mask-image: url("../icons/sprite.svg#arrow");
  mask-repeat: no-repeat;
  mask-position: -10px center;
  mask-size: cover;
  background: currentColor;
  content: "";
  transition: 0.3s;
}
.program__link:active {
  opacity: 0.3;
}

.workflow {
  box-sizing: border-box;
  padding-top: 6px;
  padding-bottom: 50px;
}
@media (--not-narrow-vp) {
  .workflow {
    padding-top: 35px;
    padding-bottom: 25px;
  }
}
@media (--wide-vp) {
  .workflow {
    padding-top: 60px;
    padding-bottom: 25px;
  }
}

.workflow__title {
  padding: 0;
  margin: 0 0 42px 0;
  font-weight: 400;
}
@media (--narrow-vp) {
  .workflow__title {
    line-height: calc(40 / 36);
  }
}
@media (--not-narrow-vp) {
  .workflow__title {
    line-height: calc(60 / 60);
  }
}
@media (--narrow-vp) {
  .workflow__title {
    font-size: 2.25rem;
  }
}
@media (--not-narrow-vp) {
  .workflow__title {
    font-size: 3.75rem;
  }
}
@media (--not-narrow-vp) {
  .workflow__title {
    margin: 0 0 71px 0;
  }
}

.workflow__list {
  display: grid;
  counter-reset: li;
  row-gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
