    column-gap: 80px;
  }
}
.form-group__list--master {
  row-gap: 15px;
}
@media (--not-narrow-vp) {
  .form-group__list--master {
    row-gap: 20px;
  }
}
@media (--wide-vp) {
  .form-group__list--master {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 80px;
  }
}
@media (--not-narrow-vp) {
  .form-group__list--purpose {
    row-gap: 23px;
  }
}

@media (--wide-vp) {
  .form__group--purpose,
.form__group--cat-info {
    width: 100%;
  }
}

.form-group__item {
  display: flex;
  flex-grow: 1;
  align-items: baseline;
}
.form-group__item--checkbox {
  padding-left: 37px;
}

.form-group__label {
  text-transform: uppercase;
  margin-right: 11px;
  transition: 0.3s;
}
@media (--not-narrow-vp) {
  .form-group__label {
    margin-right: 19px;
  }
}
.form-group__label--radio {
  position: relative;
  cursor: pointer;
}
.form-group__label--radio::before {
  position: absolute;
  top: 0;
  left: -40px;
  display: block;
  width: 30px;
  height: 30px;
  content: "";
  border: 1px solid var(--color__light-grey);
  border-radius: 50%;
  background-color: var(--color__white);
  transition: 0.3s;
}
@media (--not-narrow-vp) {
  .form-group__label--radio::before {
    width: 40px;
    height: 40px;
  }
}
.form-group__label--radio::after {
  position: absolute;
  top: 10px;
  left: -30px;
  display: none;
  width: 13px;
  height: 13px;
  content: "";
  border-radius: 50%;
  background-color: var(--color__green);
}
@media (--not-narrow-vp) {
  .form-group__label--radio::after {
    top: 12px;
    left: -28px;
    width: 18px;
    height: 18px;
  }
}
.form-group__label--radio:hover::before {
  border-color: #bababa;
}
.form-group__label--checkbox {
  position: relative;
  padding-left: 7px;
  cursor: pointer;
}
.form-group__label--checkbox::before {
  position: absolute;
  top: 0;
  left: -40px;
  display: block;
  width: 30px;
  height: 30px;
  content: "";
  border: 1px solid var(--color__light-grey);
  background-color: var(--color__white);
  transition: 0.3s;
}
@media (--narrow-vp) {
  .form-group__label--checkbox::before {
    width: 30px;
    height: 30px;
  }
}
@media (--not-narrow-vp) {
  .form-group__label--checkbox::before {
    width: 40px;
    height: 40px;
  }
}
.form-group__label--checkbox:hover::before {
  border-color: #bababa;
}
.form-group__label--checkbox::after {
  position: absolute;
  top: 7px;
  left: -33px;
  display: none;
  width: 26px;
  height: 20px;
  content: "";
  background-image: url("../icons/sprite.svg#ok");
  background-repeat: no-repeat;
}
@media (--not-narrow-vp) {
  .form-group__label--checkbox::after {
    width: 27px;
    height: 27px;
  }
}

.form-group__checkbox:checked + label::after {
  display: block;
}

.form-group__radio:checked + label::after {
  display: block;
}

.form-group__input {
  justify-content: flex-end;
  flex: 1 1 auto;
  min-height: 39px;
  border: 1px solid #e7e7e7;
  outline: none;
  appearance: textfield;
}
.form-group__input:hover {
  border-color: #cdcdcd;
}
.form-group__input:focus {
  outline: 2px solid var(--color__dark-grey);
}
.form-group__input--error {
  outline: 2px solid var(--color__form-input-error);
}
@media (--not-narrow-vp) {
  .form-group__input {
    min-height: 45px;
  }
}
.form-group__input--comment {
  resize: vertical;
  min-height: 145px;
  width: 100%;
}
@media (--not-narrow-vp) {
  .form-group__input--comment {
    min-height: 168px;
    width: 97%;
  }
}
.form-group__input:focus + .form-group__icon {
  background: var(--color__green);
}
.form-group__input--error + .form-group__icon {
  background: var(--color__form-input-error);
}

.form__button {
  margin-bottom: 15px;
}

.form-group__icon {
  position: relative;
  top: 3px;
  left: -40px;
  width: 22px;
  height: 22px;
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: cover;
  background: currentColor;
}
.form-group__icon--email {
  mask-image: url("../icons/sprite.svg#email");
}
@media (--narrow-vp) {
  .form-group__icon--email {
    left: -30px;
  }
}
@media (--not-narrow-vp) {
  .form-group__icon--email {
    left: -40px;
  }
}
.form-group__icon--phone {
  mask-image: url("../icons/sprite.svg#phone");
}
@media (--narrow-vp) {
  .form-group__icon--phone {
    left: -25px;
  }
}
@media (--not-narrow-vp) {
  .form-group__icon--phone {
    left: -35px;
  }
}

.extra {
  list-style-type: none;
  padding-bottom: 32px;
  padding-left: 0;
}
@media (--not-narrow-vp) {
  .extra {
    padding-bottom: 68px;
  }
}
@media (--wide-vp) {
  .extra {
    width: 95%;
    grid-column: 1/2;
    grid-row: 2/3;
    padding: 0;
  }
}

.extra__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 13px;
  padding-bottom: 15px;
  align-items: baseline;
  border-top: 1px solid var(--color__light-grey);
  border-bottom: 1px solid var(--color__light-grey);
}
@media (--not-narrow-vp) {
  .extra__item {
    grid-template-columns: 60% 25% auto;
    padding: 25px 37px;
    border: 1px solid var(--color__light-grey);
  }
}
@media (--wide-vp) {
  .extra__item {
    /*grid-template-columns: 290px 80px 245px 80px 245px;*/
    grid-template-columns: 27% 9.5% 27% 9.5% 27%;
    padding: 8px 0;
    border-left: none;
    border-right: none;
  }
}

.extra__title {
  grid-row: 1/-1;
  margin-bottom: 10px;
  padding: 0;
  font-family: var(--accent-font);
  font-weight: 400;
  text-transform: uppercase;
}
@media (--narrow-vp) {
  .extra__title {
    line-height: calc(16 / 16);
  }
}
@media (--not-narrow-vp) {
  .extra__title {
    line-height: calc(20 / 20);
  }
}
@media (--narrow-vp) {
  .extra__title {
    font-size: 1rem;
  }
}
@media (--not-narrow-vp) {
  .extra__title {
    font-size: 1.25rem;
  }
}
@media (--not-narrow-vp) {
  .extra__title {
    grid-column: 1/2;
    margin-bottom: 25px;
  }
}
@media (--wide-vp) {
  .extra__title {
    margin-bottom: 0;
  }
}

.extra__amount {
  display: block;
  margin-bottom: 12px;
  grid-column: 1/2;
  font-family: var(--main-font);
}
@media (--narrow-vp) {
  .extra__amount {
    line-height: calc(20 / 14);
  }
}
@media (--not-narrow-vp) {
  .extra__amount {
    line-height: calc(20 / 16);
  }
}
@media (--narrow-vp) {
  .extra__amount {
    font-size: 0.875rem;
  }
}
@media (--not-narrow-vp) {
  .extra__amount {
    font-size: 1rem;
  }
}
@media (--not-narrow-vp) {
  .extra__amount {
    grid-column: 2/3;
    text-align: right;
  }
}
@media (--wide-vp) {
  .extra__amount {
    grid-column: 3/4;
    text-align: left;
  }
}

.extra__count {
  grid-column: 2/3;
  text-align: right;
  font-family: var(--main-font);
  font-weight: 400;
}
@media (--narrow-vp) {
  .extra__count {
    line-height: calc(20 / 14);
  }
}
@media (--not-narrow-vp) {
  .extra__count {
    line-height: calc(20 / 16);
  }
}
@media (--narrow-vp) {
  .extra__count {
    font-size: 0.875rem;
  }
}
@media (--not-narrow-vp) {
  .extra__count {
    font-size: 1rem;
  }
}
@media (--not-narrow-vp) {
  .extra__count {
    grid-column: 3/4;
  }
}
@media (--wide-vp) {
  .extra__count {
    grid-column: 4/5;
    text-align: left;
  }
}

.extra__link {
  grid-column: 1/-1;
}
@media (--wide-vp) {
  .extra__link {
    grid-column: 5/6;
  }
}